import React, { Component } from "react";
import { Button, Select, Menu, Dropdown, Icon, } from "antd";
import './index.less';
import platform from "Pub/js/platform";
const { NCButton } = platform.base;
const ButtonGroup = Button.Group;
/**
 * datasource - 按钮描述 数据源
 * {
        code:'addAppClass', - 按钮编码
        name: "增加应用分类", - 按钮名称
        type: "primary", - 按钮类型
        group: true, - 需要按钮组组内按钮
        isshow:true - 是否显示
        divide: 组合分割按钮（参数‘btn’和‘divide’）
    },
 * onClick - 按钮 Click 事件
 */
class CreateButton extends Component {
    constructor(props) {
        super(props);
    }

    
    createBtn = btns => {
        return btns.map(item => {
            if (item.isshow) {
                if (item.divide == 'btn') {
                    return (
                        <NCButton
                            className='divide-btn'
                            key={item.code}
                            colors={item.type}
                            fieldid={item.fieldid}
                            onClick={() => {
                                this.props.onClick(item.code);
                            }}
                        >
                            {item.name}
                        </NCButton>
                    );
                } else if (item.divide == 'select') {
                    //分割按钮
                    var menu = (
                        <Menu 
                            onClick={() => {
                                this.props.onClick(item.code);
                            }}
                        >
                            <Menu.Item key="1">
                                <div fieldid={item.code}>{item.name}</div>
                            </Menu.Item>
                        </Menu>
                    );
                    return (
                        <Dropdown overlay={menu} trigger={['click']} overlayClassName={`fieldid_${item.dropdownFieldid}_list`}>
                            <NCButton
                                className='margin-left-1'
                                key={item.code}
                                colors={item.type}
                                fieldid={item.fieldid}
                                is_arrow={true}
                                style={{ minWidth: 18 }}
                            >
                                <i className="iconfont icon-hangcaozuoxiala1"></i>
                            </NCButton>
                        </Dropdown>
                    );
                } else {
                    return (
                        <NCButton
                            className={`margin-left-6 ${
                                item.className ? item.className : ""
                            }`}
                            key={item.code}
                            colors={item.type}
                            fieldid={item.fieldid}
                            onClick={() => {
                                this.props.onClick(item.code);
                            }}
                        >
                            {item.name}
                        </NCButton>
                    );
                }
            } else {
                return null;
            }
        });
    };
    createBtnGroup = btnGroup => {
        if (btnGroup.length > 0) {
            return <ButtonGroup>{this.createBtn(btnGroup)}</ButtonGroup>;
        } else {
            return null;
        }
    };
    createButton = dataSource => {
        // 按钮组
        let btnGroup = dataSource.filter(item => item.group);
        // 独立按钮
        let btns = dataSource.filter(item => !item.group);
        return (
            <div>
                {this.createBtn(btns)}
                {this.createBtnGroup(btnGroup)}
            </div>
        );
    };
    render() {
        return (
            <div
                className={`buttons-component ${
                    this.props.className ? this.props.className : ""
                }`}
            >
                {this.createButton(this.props.dataSource)}
            </div>
        );
    }
}
export default CreateButton;
